<template>
  <div class="seller-management" style="padding-top: 44px;">
    <backer-header title="商品管理" />
    <ul class="tabs">
      <li :class="{active: active === 0}" @click="changeTab(0)">待审核<i></i></li>
      <li :class="{active: active === 1}" @click="changeTab(1)">已上架<i></i></li>
      <li :class="{active: active === 2}" @click="changeTab(2)">已下架<i></i></li>
      <li :class="{active: active === 3}" @click="changeTab(3)">审核被拒<i></i></li>
    </ul>
    <scroll clickable ref="scroll" class="scroll" @scrollToEnd="scrollToEnd" :is-finished="finished">
      <div class="goods" v-for="(item, index) in list" :key="item.id">
        <div class="info">
          <div class="img" :style="{backgroundImage: `url(${item.thumb_urls.split(',')[0]})`}"></div>
          <div class="right">
            <h4 class="van-multi-ellipsis--l2">{{ item.title }}</h4>
            <div class="size">
              <span>{{ item.category.title }}</span>
              <span>已售{{ item.sale_count }}件</span>
            </div>
            <p class="price">¥{{ item.price }}</p>
          </div>
        </div>
        <div class="btns">
          <button @click="toggleGoods(item.id, index)" v-if="active===1 || active===2">{{ active === 1 ? '下架商品' : '上架' }}</button>
          <button @click="$router.push('/seller/goods/edit/' + item.id)">编辑商品</button>
        </div>
      </div>
    </scroll>
  </div>
</template>

<script>
import BackerHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'

const PAGE_SIZE = 10

export default {
  name: 'seller-management',
  components: {
    BackerHeader,
    Scroll
  },
  data() {
    return {
      active: 0,
      list: [],
      page: 1,
      loading: false,
      finished: true
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    toggleGoods(id, index) {
      const loading = this.$toast.loading()
      post('/shop/shop/setGoodsSaleStatus', {
        status: this.active === 1 ? 2 : 1,
        goodsid: id
      })
        .then(() => {
          this.list.splice(index, 1)
          loading.close()
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
          })
        })
        .catch(() => {
          setTimeout(() => {
            loading.close()
          }, 1000)
        })
    },
    changeTab(tab) {
      if (tab === this.active) return
      this.active = tab
      this.page = 1
      this.list = []
      this.getList()
    },
    scrollToEnd() {
      if (this.loading) return
      this.loading = true
      this.page++
      this.getList()
    },
    getList() {
      let loading = null
      if (this.page === 1) {
        loading = this.$skeLoading('.seller-management', '100px')
      }
      post('/shop/shop/getShopGoods', {
        type: this.active,
        page: this.page,
        size: PAGE_SIZE
      })
        .then(({ data: { data } }) => {
          this.list = this.list.concat(data)
          this.finished = data.length < PAGE_SIZE
          loading && loading.close()
          this.$nextTick(() => {
            this.$refs.scroll.refresh()
            this.loading = false
          })
        })
        .catch(() => {
          loading && loading.close()
        })
    }
  }
}
</script>

